window.jQuery = (selectorOrArrayOrTemplate) => {
	let elements
	if (typeof selectorOrArrayOrTemplate === 'string') {
		if (selectorOrArrayOrTemplate[0] === '<') {
			elements = [createElement(selectorOrArrayOrTemplate)]
		} else {
			elements = document.querySelectorAll(selectorOrArrayOrTemplate)
		}
	} else if (selectorOrArrayOrTemplate instanceof Array) {
		elements = selectorOrArrayOrTemplate
	}

	function createElement(string) {
		const node = document.createElement('template')
		node.innerHTML = string.trim()
		node.insert
		return node.content.firstChild
	}
	// é‡ç‚¹1
	const api = Object.create(jQuery.prototype)
	Object.assign(api, {
		elements: elements,
		oldApi: selectorOrArrayOrTemplate.oldThis,
	})
	return api
}
jQuery.prototype = {
	construct: jQuery,
	jquery: true,
	delegate: {
		userFn: null,
		dgFn: null,
		selector: '',
	},
	// äº‹ä»¶ç»‘å®šæ–¹æ³•
	// 2 ä¸ªå‚æ•°ï¼Œå…ƒç´ æœ¬èº«çš„äº‹ä»¶ç»‘å®š
	// 3 ä¸ªå‚æ•°ï¼Œäº‹ä»¶å§”æ‰˜
	on(eventType, selector, fn) {
		if (arguments.length === 3) {
			Object.assign(this.delegate, {
				userFn: fn,
				dgFn: null,
				selector: selector,
			})
			this.delegate.dgFn = (e) => {
				let el = e.target
				this.each((node) => {
					let temp = el
					while (!temp.matches(selector)) {
						if (temp === node) {
							temp = null
							break
						}
						if (!('matches' in temp.parentNode)) {
							break
						}
						temp = temp.parentNode
					}

					temp && node.contains(temp) && fn.call(el, e, el)
				})
			}
			this.each((node) => {
				node.addEventListener(eventType, this.delegate.dgFn)
			})
		} else if (arguments.length === 2 && arguments[1] instanceof Function) {
			this.each((node) => {
				node.addEventListener(eventType, arguments[1])
			})
		}
		return this
	},
	// å–æ¶ˆäº‹ä»¶ç»‘å®š
	off(eventType, selector, fn) {
		if (arguments.length === 3) {
			this.each((node) => {
				if (
					selector === this.delegate.selector &&
					fn === this.delegate.userFn
				) {
					node.removeEventListener(eventType, this.delegate.dgFn)
				}
			})
		} else if (arguments.length === 2 && arguments[1] instanceof Function) {
			this.each((node) => {
				node.removeEventListener(eventType, arguments[1])
			})
		}
		return this
	},
	// æ·»åŠ ä¸ºèŠ‚ç‚¹çš„å­å…ƒç´ 
	// é»˜è®¤ç»™ç¬¬ä¸€ä¸ªå…ƒç´ æ·»åŠ å­å…ƒç´ 
	append(newNode) {
		if (newNode instanceof Element) {
			this.get(0).appendChild(newNode)
		} else if (newNode instanceof HTMLElement) {
			for (let i = 0; i < newNode.length; i++) {
				this.get(0).appendChild(newNode[i])
			}
		} else if (newNode.jquery === true) {
			newNode.each((node) => {
				this.get(0).appendChild(node)
			})
		}
		return this
	},
	// ç»™å…ƒç´ æ·»åŠ æ–°çš„ç±»
	addClass(className) {
		this.each((node) => {
			node.classList.add(className)
		})
		return this
	},
	// æŸ¥æ‰¾å…ƒç´ 
	find(selector) {
		let array = []
		// const elements = this.elements
		// for (let i = 0; i < elements.length; i++) {
		// 	array = array.concat(Array.from(elements[i].querySelectorAll(selector)))
		// }
		this.each((node) => {
			array.push(...node.querySelectorAll(selector))
		})
		array.oldThis = this
		return jQuery(array)
	},
	// è¿”å›åˆ°ä¸Šä¸€ä¸ªthiså¯¹è±¡
	end() {
		return this.oldApi
	},
	// éå†å…ƒç´ 
	each(fn) {
		const elements = this.elements
		for (let i = 0; i < elements.length; i++) {
			fn.call(null, elements[i], i)
		}
	},
	// è·å–çˆ¶å…ƒç´ 
	parent() {
		let arr = []
		this.each((node) => {
			if (!arr.includes(node.parentNode)) {
				arr.push(node.parentNode)
			}
		})
		return jQuery(arr)
	},
	// è·å–å­å…ƒç´ 
	children() {
		let arr = []
		this.each((node) => {
			// ä½¿ç”¨å±•å¼€è¿ç®—ç¬¦æ›´æ–¹ä¾¿
			arr.push(...node.children)
			// arr = arr.concat(Array.from(node.children))
		})
		return jQuery(arr)
	},
	// è·å–å…„å¼ŸèŠ‚ç‚¹
	siblings() {
		let arr = []
		this.each((node) => {
			for (let i = 0; i < node.parentNode.children.length; i++) {
				Array.from(node.parentNode.children).filter((item) => {
					if (item !== node && !arr.includes(item)) {
						arr.push(item)
					}
				})
			}
		})
		return jQuery(arr)
	},
	// è·å–ä¸‹ä¸€ä¸ªå…ƒç´ 
	next() {
		let arr = []
		for (let i = 0; i < elements.length; i++) {
			let sbl = elements[i].nextSibling
			while (3 === sbl.nodeType) {
				sbl = sbl.nextSibling
			}
			arr.push(sbl)
		}
		return jQuery(arr)
	},
	// è·å–ä¸Šä¸€ä¸ªå…ƒç´ 
	previous() {
		let arr = []
		for (let i = 0; i < elements.length; i++) {
			let pre = elements[i].previousSibling
			while (3 === pre.nodeType) {
				pre = pre.previousSibling
			}
			arr.push(pre)
		}
		return jQuery(arr)
	},
	// è·å–å…ƒç´ æ‰€åœ¨ä½ç½®çš„ä¸‹æ ‡ã€‚
	index() {
		let indexArr = []
		for (let i = 0; i < elements.length; i++) {
			let pc = elements[i].parentNode.children
			let j
			for (j = 0; j < pc; j++) {
				if (elements[i] === pc[j]) {
					break
				}
			}
			indexArr.push(j)
		}
		return indexArr
	},
	// è·å–åœ¨elementsä¸­ç¬¬indexä¸ªå…ƒç´ 
	get(index) {
		return this.elements[index]
	},
}
